body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* ================================= 
  Base Element Styles
==================================== */

* {
	box-sizing: border-box;
}
body {
	font-family: 'Varela Round', sans-serif;
	line-height: 1.6;
	color: #3a3a3a;
	margin: 0;
}
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
p {
	font-size: .95em;
}
h2,
h3,
a {
	color: #093a58;
}
h1,
h2,
h3 {
	margin-top: 0;
}
a {
	text-decoration: none;
}
img {
	max-width: 100%;
}

/* ================================= 
  Base Layout Styles
==================================== */

/* ---- Navigation ---- */
.main-header {
  display: flex;
  flex-direction: column;
  position: fixed;
  width: 100%;
  background: #fff;
  box-shadow: 0 4px 3px rgba(0,0,0,.5);
  z-index: 1001;
}
.main-title {
  display: flex;
  padding: 0 8px;
}
.name {
	font-size: 1.35em;
	margin: 0;
}
.navigation-bar {
  width: 50px;
  height: 50px;
  padding: 10px;
  margin-left: auto;
  cursor: pointer;
}
.close-button {
  width: 44px;
  height: 44px;
  padding: 6px;
  margin: 9px 11px 0px auto;
  cursor: pointer;
}
.main-nav {
  position: fixed;
  top: 0;
  right: -206px;
  display: flex;
  flex-direction: column;
  min-width: 200px;
  height: 100%;
  max-height: 100%;
  box-shadow: -3px 0px 3px rgba(0,0,0,.5);
  overflow-y: auto;
  -webkit-transition: right ease-in-out .5s, background ease-in-out .5s;
  transition: right ease-in-out .5s, background ease-in-out .5s;
}
.main-nav.show {
  right: 0px;
  background: #fff;
}
.main-nav li {
	text-align: center;
}
.name button,
.main-nav button {
  width: 100%;
  padding: 10px 15px;
  background: transparent;
  border: none;
  cursor: pointer;
  text-align: center;
  font: unset;
  outline: unset;
}
.navy-blue {
  color: #093a58;
}
.light-orange {
  color: #F2994A;
}
.main-nav button:hover {
	color: #093a58;
}

/* ---- Layout Containers ---- */

.main-header {
	padding-top: .35em;
	padding-bottom: .35em;
}
.content {
  max-width: 1200px;
}
.banner,
.main-footer {
	text-align: center;
}
.banner {
  display: flex;
  flex-direction: column;
  justify-content: center;
	color: #fff;
  background: -webkit-linear-gradient(#F2994A, transparent 90%),
              -webkit-linear-gradient(bottom, #fff, transparent),
              #F2994A url(/remote-assignments/week-4/react-app/static/media/josue-isai-ramos-figueroa-587073-unsplash.068049c5.jpg) no-repeat center;
  background: linear-gradient(#F2994A, transparent 90%),
              linear-gradient(0deg, #fff, transparent),
              #F2994A url(/remote-assignments/week-4/react-app/static/media/josue-isai-ramos-figueroa-587073-unsplash.068049c5.jpg) no-repeat center;
  background-size: cover;
	padding: 1em 0;
  margin-bottom: 24px;
  min-height: 300px;
}
.row-title,
.row-footer {
  text-align: center;
}
.row {
  display: flex;
  flex-direction: column;
  max-width: 1200px;
  margin: 0 auto;
}
.row-hidden {
  display: none;
}
.col {
	padding: 1em;
}
.main-footer {
	background: #d9e4ea;
	padding: 2em 0;
	margin-top: 30px;
}

/* ---- Page Elements ---- */

.headline {
  margin-bottom: 0;
  font-size: 1.8em;
}
.card {
  border-radius: .28571429rem;
  box-shadow: 0 1px 3px 0 #d4d4d5, 0 0 0 1px #d4d4d5;
}
.card-img {
  border-radius: .28571429rem .28571429rem 0 0;
}
.card-content {
  padding: 1em;
}
.ui.button {
  cursor: pointer;
  display: inline-block;
  outline: 0;
  border: none;
  background: transparent none;
  box-shadow: 0 0 0 2px #093a58 inset;
  color: #093a58;
  padding: .78571429em 1.5em .78571429em;
  font-weight: 700;
  text-align: center;
  border-radius: .28571429rem;
  -webkit-transition: opacity .1s ease,background-color .1s ease,color .1s ease,box-shadow .1s ease,background .1s ease;
  transition: opacity .1s ease,background-color .1s ease,color .1s ease,box-shadow .1s ease,background .1s ease;
}
.ui.button:hover,
.ui.button:active {
  box-shadow: none!important;
  color: #fff;
  box-shadow: 0 0 0 1px transparent inset, 0 0 0 0 rgba(34,36,38,.15) inset;
}
.ui.button:hover {
  background-color: #093a58;
}
.ui.button:active {
  background-color: #07324c;
}

/* ================================= 
  Media Queries
==================================== */

@media (min-width: 769px) {
  .footer-inner{
		width: 80%;
		margin: 0 auto;
		max-width: 1150px;
	}
	.tagline {
		font-size: 1.4em;
  }
  .ui.button {
    font-size: .8em;
  }
}
/* ================================= 
  Media Queries
==================================== */

@media (min-width: 500px) {
  .row {
    flex-direction: row;
    flex-wrap: wrap;
  }
  .col {
    width: 50%;
  }
}

@media (min-width: 800px) {
  .navigation-bar,
  .close-button {
    display: none;
  }
  .main-nav {
    position: unset;
    margin-left: auto;
    flex-direction: row;
    box-shadow: unset;
  }
  .main-header {
    flex-direction: row;
    align-items: center;
    padding-left: 10px;
    padding-right: 10px;
  }
  .banner {
    min-height: 600px;
  }
  .headline {
    font-size: 2em;
  }
}

.dimmed:after {
  position: fixed;
  top: 0;
  right: 0;
  content: '';
  background-color: rgba(0,0,0,.4);
  overflow: hidden;
  opacity: 0;
  -webkit-transition: opacity .5s;
  transition: opacity .5s;
  will-change: opacity;
  z-index: 1000;
  width: 100% !important;
  height: 100% !important;
  opacity: 1 !important;
}
